import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import "./print.css";

import IntlMessages from "../../components/utility/intlMessages";
import { siteConfig } from "../../settings";

import { Row, Col, Divider } from "antd";
import moment from "moment";
import { MiniText } from "./app.style";

export class PrintField extends Component {
  render() {
    const { label, description, bold } = this.props;
    return (
      <>
        <span style={{ fontSize: "12px" }}>{label}: </span>
        {bold ? (
          <strong style={{ fontSize: "12px" }}>{` ${
            description ? description : ""
          }`}</strong>
        ) : (
          <span style={{ fontSize: "12px" }}>{` ${
            description ? description : ""
          }`}</span>
        )}
      </>
    );
  }
}

export class FieldCol extends Component {
  render() {
    const { rightSide, leftSide } = this.props;
    return (
      <Row>
        <Col span={12}>
          {leftSide
            ? leftSide.map((data, key) => (
                <Row type="flex" key={key}>
                  <Col span={8}>
                    <span style={{ fontSize: "12px" }}>{data.label}</span>
                  </Col>
                  <Col span={1}>
                    <span style={{ fontSize: "12px" }}>:</span>
                  </Col>
                  <Col span={13}>
                    {data.bold ? (
                      <strong style={{ fontSize: "12px" }}>
                        {data.description}
                      </strong>
                    ) : (
                      <span style={{ fontSize: "12px" }}>
                        {data.description}
                      </span>
                    )}
                  </Col>
                </Row>
              ))
            : null}
        </Col>

        <Col span={12}>
          {rightSide
            ? rightSide.map((data, key) => (
                <Row type="flex" key={key}>
                  <Col span={8}>
                    <span style={{ fontSize: "12px" }}>{data.label}</span>
                  </Col>
                  <Col span={1}>
                    <span style={{ fontSize: "12px" }}>:</span>
                  </Col>
                  <Col span={15}>
                    {data.bold ? (
                      <strong style={{ fontSize: "12px" }}>
                        {data.description}
                      </strong>
                    ) : (
                      <span style={{ fontSize: "12px" }}>
                        {data.description}
                      </span>
                    )}
                  </Col>
                </Row>
              ))
            : null}
        </Col>
      </Row>
    );
  }
}

class PrintTransaction extends Component {
  render() {
    const { componentRef, user, title, subtitle, children } = this.props;
    let { company } = user ? user : [];
    company = company ? company : [];
    return (
      <div style={{ display: "none" }}>
        <div
          ref={el => componentRef(el)}
          style={{ width: "100%", maxWidth: "100%" }}
        >
          <div className="page-header-transaction">
            <Row justify="space-between">
              <Col span={16} style={{ textAlign: "left" }}>
                <Row type="flex">
                  {company.logo_small_url ? (
                    <Col span={5}>
                      <img
                        style={{
                          // width: "90%",
                          maxHeight: "90px",
                          maxWidth: "90px"
                        }}
                        src={
                          localStorage.getItem("server") +
                          company.logo_small_url
                        }
                      />
                    </Col>
                  ) : (
                    <Col span={5}>
                      <img
                        style={{
                          // width: "90%",
                          maxHeight: "90px",
                          maxWidth: "90px"
                        }}
                        src="https://via.placeholder.com/90"
                      />
                    </Col>
                  )}

                  <Col span={19} style={{ paddingLeft: "10px" }}>
                    <Row style={{ marginBottom: "7px" }}>
                      <Col>
                        <strong style={{ fontSize: "18px" }}>
                          {company.company_name}
                        </strong>
                      </Col>
                    </Row>

                    <Row style={{ lineHeight: "1.2em", marginBottom: "5px" }}>
                      <Col>
                        <span style={{ fontSize: ".7rem" }}>
                          {company.address}
                        </span>
                      </Col>
                    </Row>

                    <Row style={{ lineHeight: "1.2em" }}>
                      <Col>
                        <span style={{ fontSize: ".75rem" }}>
                          {company.district && company.city
                            ? `${
                                company.district.district_name
                                  ? company.district.district_name
                                  : ""
                              }, ${
                                company.city.city_name
                                  ? company.city.city_name
                                  : ""
                              }, ${
                                company.id_province ? company.province_name : ""
                              }
                              ${company.zip_code ? " " + company.zip_code : ""}
                            `
                            : null}
                        </span>
                      </Col>
                    </Row>

                    <Row style={{ lineHeight: "1.2em" }}>
                      <Col>
                        <span>
                          {company.phone1 && company.email
                            ? `${company.phone1}, ${company.email}`
                            : company.phone1
                            ? company.phone1
                            : null}
                        </span>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Col>
              <Col span={8} style={{ textAlign: "right" }}>
                <strong
                  style={{ textTransform: "uppercase", fontSize: "18px" }}
                >
                  {title}
                </strong>
                {subtitle ? subtitle : null}
              </Col>
            </Row>
          </div>

          <div className="page-footer">
            <div style={{ width: "100%" }}>
              <Divider style={{ margin: "0 auto 0.5em auto" }} />
            </div>

            <MiniText style={{ marginBottom: "1em" }}>
              {siteConfig.footerReport}
            </MiniText>

            <br />

            <MiniText>
              <IntlMessages
                id="printedOn"
                values={{
                  dateTime: moment().format("HH:mm - DD MMMM YYYY")
                }}
              >
                {txt => `${txt}`}
              </IntlMessages>
            </MiniText>

            {/* <Row type="flex" justify="space-between">
              <Col>
                <MiniText>
                  <IntlMessages
                    id="printedOn"
                    values={{
                      dateTime: moment().format("DD MMMM YYYY - HH:mm")
                    }}
                  >
                    {txt => `${txt}`}
                  </IntlMessages>
                </MiniText>
              </Col>
              <Col>
                <MiniText>
                  <span id="#current-page">
                    <IntlMessages id="page">{txt => `${txt}  `}</IntlMessages>
                  </span>
                  <span id="#total-page">
                    <IntlMessages id="from">{txt => `  ${txt}`}</IntlMessages>
                  </span>
                </MiniText>
              </Col>
            </Row> */}
          </div>

          <table className="table-content">
            <thead className="thead-header">
              <tr>
                <td>
                  {/* <!--place holder for the fixed-position header--> */}
                  <div className="page-header-transaction-space" />
                </td>
              </tr>
            </thead>

            <tbody>
              <tr>
                <td>
                  {/* <!--*** CONTENT GOES HERE ***--> */}
                  <Row>{children ? children : null}</Row>
                </td>
              </tr>
            </tbody>

            <tfoot className="tfoot-footer">
              <tr>
                <td>
                  {/* <!--place holder for the fixed-position footer--> */}
                  <div className="page-footer-space" />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return { user: state.auth.user };
};

export default compose(connect(mapStateToProps))(PrintTransaction);
