import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PurchaseInvoiceUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";
import HistoryPurchaseInvoice from "./history";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/purchase/purchaseInvoice/actions";
import { Box } from "../../../App/app.style";
import { Spin, Tooltip } from "antd";
import Print from "./print";

const redirecTo = "/dashboard/purchases/purchase-invoices";

class UpdatePurchaseInvoice extends Component {
  state = {
    visible: false
  };

  componentDidMount() {
    const { editPurchaseInvoice } = this.props;
    const { id } = this.props.match.params;
    editPurchaseInvoice(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.purchaseInvoice.error) {
      notification("error", this.props.purchaseInvoice.error);
      this.props.clearError();
    }
    if (this.props.purchaseInvoice.success) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.purchaseInvoice.id_purchase_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("success", this.props.purchaseInvoice.success);
      this.props.clearSuccess();
    }
    // if (this.props.purchaseInvoice.warning) {
    //   // this.props.history.replace(
    //   //   redirecTo + "/update/" + this.props.purchaseInvoice.id_purchase_invoice
    //   // );
    //   //   this.props.history.replace(redirecTo);
    //   notification("warning", this.props.purchaseInvoice.warning);
    //   this.props.clearSuccess();
    // }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmitCancel = value => {
    const { cancelPurchaseInvoice } = this.props;
    const { id } = this.props.match.params;

    cancelPurchaseInvoice(id, value);
  };

  handleSubmit = value => {
    const { updatePurchaseInvoice } = this.props;
    const { id } = this.props.match.params;
    updatePurchaseInvoice(id, value);
  };

  render() {
    // console.log(this.props);
    const {
      loading,
      loadingForeign,
      loadingSubmit
    } = this.props.purchaseInvoice;
    const purchaseInvoice = this.props.purchaseInvoice.purchaseInvoice
      ? this.props.purchaseInvoice.purchaseInvoice
      : [];
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="purchaseInvoices.update.title" />}
            marginBottom={true}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print
              loading={loading}
              data={purchaseInvoice}
              purchaseInvoice={this.props.purchaseInvoice}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirecTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <HistoryPurchaseInvoice
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={purchaseInvoice ? purchaseInvoice.description : null}
          />

          {loading && loadingForeign && !loadingSubmit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <PurchaseInvoiceUpdateForm
              submit={this.handleSubmit}
              handleSubmitCancel={this.handleSubmitCancel}
              purchaseInvoice={this.props.purchaseInvoice}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    purchaseInvoice: state.purchaseInvoice,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdatePurchaseInvoice);
